.widget {
    float: left;
    width: 20%;
    background-color: white;
    height: 100vh;
    padding-right: 7px;
    padding-left: 7px;
}

.widget_img {
    text-align: center;
    padding-top: 30px;
    margin-bottom: 20px;
    background-color: whitesmoke;
    border-radius: 10px;
}

.widget_img h4 {
    margin-bottom: 15px;
    margin-top: 5px;
}

.widget_img img {
    width: 200px;
    margin-left: 45px;
    margin-bottom: 20px;
    height: 200px;
    border-radius: 50%;
}

.widgetadmin {
    width: 100%;
}

.widget_btn {
    width: 70%;
    background-color: rgb(224, 52, 127);
    border-radius: 20px;
    padding: 7px;
    border: none;
    margin-left: 10px;
    margin-bottom: 20px;
    color: white;
    cursor: pointer;
}

.widget_title {
    padding-top: 0.4rem;
}

.widget_post {
    text-align: center;
    background-color: whitesmoke;
    width: 100%;
    height: auto;
    padding: 8px;
    border-radius: 10px;
    margin-top: 10px;
}

.widget_post h4 {
    margin-bottom: 15px;
}

.post_card {
    background-color: white;
    width: 100%;
    display: grid;
    padding: 0.2rem;
    margin-bottom: 10px;
    grid-template-columns: 25% 50% 25%;
    border-radius: 10px;
}

.user1_img {
    margin: 0.8rem;
}
.user1_img img {
    width: 37px;
    height: 37px;
}

.user1_id {
    padding-top: 9px;
}

.user1_score {
    width: 63px;
    height: 60px;
    background-color: gold;
    /* padding: .8rem; */
    padding: 25% 18%;
    border-radius: 50%;
    margin-top: 9px;
    margin-right: 90px;
    font-size: 11px;
}

/* phone-only */
@media (max-width: 599px) {
    .widget {
        float: left;
        width: 100%;
        background-color: white;
        height: 100vh;
        padding-right: 7px;
        padding-left: 7px;
    }

    .widget_img {
        background-color: whitesmoke;
        margin-top: 20px;
        border-radius: 10px;
    }

    .widget_img h4 {
        margin-bottom: 15px;
        margin-top: 5px;
    }

    .widget_img img {
        width: 68%;
        margin-left: 45px;
        margin-bottom: 20px;
        /* height: 160px; */
        border-radius: 50%;
        display: none;
    }
}

