.goBack_btn {
    padding: 0.5rem;
}

.question_table {
    padding: 3rem;
}

table,
th,
td {
    border-bottom: 1px solid lightgray;
    border-top: 1px solid black;
    border-left: 1px solid lightgray;
    border-right: 1px solid lightgray;
}

th {
    font-weight: bold;
    font-size: 1rem;
    padding: 4px;
}

table {
    border-collapse: collapse;
    width: 100%;
}

td {
    text-align: center;
    font-size: 12px;
    padding: 0.5rem;
}

.table_btn {
    padding-right: 4px;
    padding-left: 4px;
    border-radius: 10px;
    width: 70px;
    background: rgb(245, 197, 76);
    color: black;
    border: none;
    margin-bottom: 4px;
    margin-right: 7px;
    cursor: pointer;
    font-weight: 700;
}

.table_btn2 {
    padding-right: 4px;
    padding-left: 4px;
    border-radius: 10px;
    width: 70px;
    background: rgb(242, 111, 51);
    color: black;
    border: none;
    margin-right: 7px;
    cursor: pointer;
    font-weight: 700;
}
