.navbar {
    display: flex;
    padding-top: 10px;
}

.navbar h5 {
    padding: 1rem;
}

.navbar_username {
    margin-right: 20px;
}

.navbar_img {
    margin-left: 74%;
}

.navbar_img img {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    margin-bottom: 10px;
}

.navbar_username {
    margin-left: 17px;
    margin-top: 17px;
}

/* phone-only */
@media (max-width: 599px) {
    .navbar {
        display: none;
        padding-top: 10px;
    }
}

