a {
    text-decoration: none;
    color: black;
}

.infobox {
    width: 100%;
    height: 7rem;
    background-color: rgb(233, 224, 224);
    border-radius: 10px;
    padding: 1rem;
    margin-bottom: 1rem;
}

.grid {
    display: grid;
    grid-template-columns: 80% 20%;
}
.card {
    width: 100%;
    height: 4rem;
}

.card svg {
    font-size: 2rem;
    margin-top: 2rem;
}
