.image-container {
    height: 150px;
    width: 150px;
    border-radius: 100%;
    display: flex;
    align-items: center;
    margin-top: 20px;
    justify-content: center;
    border: 1px solid rgb(192, 192, 231);
}

.image-container img {
    width: 130px;
    height: 140px;
}

.login-page {
    width: 80%;
    margin-top: 20px;
    margin-right: 10%;
    margin-left: 10%;
    display: flex;
    height: auto;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.login {
    display: flex;
    flex-direction: column;
    width: 40%;
    margin-top: 10px;
    align-items: center;
}

@media screen and (max-width: 600px) {
    .login {
        width: 80%;
    }
}

.login-form-inputs {
    width: 100%;
    display: grid;
    grid-row-gap: 10px;
    padding-top: 20px;
}

.login-form-inputs input {
    width: 100%;
    padding: 15px 20px;
    border-radius: 30px;
    border: 1px solid lightgray;
}

.submit-btn {
    border-radius: 10px;
    border: none;
    width: 200px;
    height: 40px;
    font-size: 0.8rem;
    font-weight: bold;
    background-color: gold;
    margin-bottom: 5px;
    margin-top: 5px;
    cursor: pointer;
}

.submit-btn:hover {
    background-color: rgb(235, 207, 51);
}

.forgot-password {
    margin-left: auto;
    font-size: 16px;
    font-weight: bold;
    margin-top: 5px;
}

.dont-have-account {
    font-size: 0.6rem;
    font-weight: bold;
    margin-bottom: 10px;
}
