.navigation {
    width: 100%;
    height: 6rem;
    padding: 1rem 5rem;
    display: block;
    background: #5759c6;
}

.title {
    width: 30%;
    height: 4rem;
    display: block;
    color: white;
    padding: auto;
    float: left;
}

.title h3 {
    font-size: 2rem;
    margin-top: 1rem;
}

.btnContainer {
    width: 40%;
    height: 4rem;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    float: right;
    padding-top: 1rem;
}

.btnContainer h3 {
    color: white;
    float: left;
    margin-right: 1rem;
}

.paddingTop {
    margin-bottom: 5px;
    padding-top: 10px;
    width: auto;
}
/* phone-only */
@media (max-width: 599px) {
    .navigation {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        width: 100%;
        height: 5rem;
        padding: 0.5rem 0.5rem;
    }
    .title {
        width: 40%;
        height: auto;
        display: flex;
        justify-content: flex-end;
    }
    .title h3 {
        font-size: 1rem;
        margin-top: 1.5rem;
        margin-right: 4rem;
    }
    .btnContainer {
        display: flex;
        flex-direction: row;
        padding-top: 1rem;
        width: 60%;
    }
    .paddingTop {
        margin-bottom: 5px;
        padding-top: 10px;
        width: auto;
        font-size: 10px;
    }
}

