/* tablet-portait-only */
@media (min-width: 600px) and (max-width: 899px) {
    .navigation {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        width: 100%;
        height: 6rem;
        padding: 1rem 3.5rem;
    }
    .title h3 {
        font-size: 1.2rem;
        margin-top: 1.5rem;
        margin-right: 1.5rem;
    }
    .btnContainer {
        display: flex;
        flex-direction: row;
        padding-top: 1rem;
        width: 60%;
    }
    .paddingTop {
        margin-bottom: 5px;
        padding-top: 7px;
        width: auto;
        font-size: 10px;
    }
}
